import React, { useState, useRef, useEffect, useCallback } from 'react';
import { Typography, TextField, Button, Grid, FormControlLabel, Checkbox, Box, Snackbar, InputAdornment } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import MuiAlert from '@mui/material/Alert';

const Page = () => {
  const [message, setMessage] = useState('');
  const [lookalikesValue, setLookalikesValue] = useState(true);
  const [toLowerCaseValue, setToLowerCaseValue] = useState(true);
  const [chattyValue, setChattyValue] = useState(false);
  const [startValue, setStartValue] = useState('\\b');
  const [endValue, setEndValue] = useState('\\b');
  const [error, setError] = useState(false);
  const [outputMessage, setOutputMessage] = useState('');
  const inputRef = useRef(null);
  const [isCopied, setIsCopied] = useState(false);

  useEffect(() => {
    document.title = `LsCo Software - RegEx Generator`;
  }, []);

  const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });

  const handleClick = useCallback(
    (msg) => {
      setError(false);
      const trimmedMessageArray = msg.split('\n').map((i) => i.trim());

      const list = [];

      for (let i = 0; i < trimmedMessageArray.length; i++) {
        const filteredRegex = /[a-z0-9]/gi;
        const filteredMessage = trimmedMessageArray[i].match(filteredRegex);

        if (!filteredMessage) {
          continue;
        }

        const regex = filteredMessage
          .map((char) => {
            const replacerRegex = new RegExp(
              `[${
                toLowerCaseValue ? (lookalikesValue ? replacer(char.toLowerCase()) : char) : lookalikesValue ? replacer(char) : char
              }][\\W_]*?`,
              'gi'
            );
            return `(${replacerRegex.source})+`;
          })
          .join('');

        list.push(regex);
      }

      const output = list
        .map((i) => {
          return `${chattyValue ? 'config:any regi:' : ''}${startValue}${i}${endValue}`;
        })
        .join('\n');

      setOutputMessage(output);
    },
    [chattyValue, lookalikesValue, toLowerCaseValue, startValue, endValue]
  );

  const handleCopyClick = () => {
    navigator.clipboard.writeText(outputMessage);
    setIsCopied(true);
  };

  const handleClose = () => {
    setIsCopied(false);
  };

  useEffect(() => {
    handleClick(message);
  }, [message, chattyValue, lookalikesValue, toLowerCaseValue, handleClick]);

  function replacer(x) {
    const lookalikes = [
      {
        char: 'a',
        replacements: ['', '@', '4', '%']
      },
      {
        char: 'e',
        replacements: ['', '', '3']
      },
      {
        char: 'g',
        replacements: ['9']
      },
      {
        char: 'i',
        replacements: ['|', '1', '!']
      },
      {
        char: 'o',
        replacements: ['', '0']
      },
      {
        char: 's',
        replacements: ['$']
      },
      {
        char: 'u',
        replacements: ['']
      }
    ];

    const find = lookalikes.find((i) => i.char === x);

    if (!find) return x;

    return x + find.replacements.join('');
  }

  return (
    <MainCard title="RegEx Generator" description="You can create an regular expression for you benefit.">
      <Typography variant="body2">
        <Grid container spacing={2} alignItems="center">
          <Grid item xs={12}>
            <TextField
              fullWidth
              multiline
              id="standard-basic"
              label="Message"
              type="text"
              variant="standard"
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              error={error}
              helperText={error ? 'This field is required' : ''}
              inputRef={inputRef}
              onInput={(event) => handleClick(event.target.value)}
            />
          </Grid>
          <Grid item xs={6}>
            <FormControlLabel
              control={<Checkbox checked={lookalikesValue} onChange={(e) => setLookalikesValue(e.target.checked)} defaultChecked />}
              label="Lookalikes"
            />
            <FormControlLabel
              control={<Checkbox checked={toLowerCaseValue} onChange={(e) => setToLowerCaseValue(e.target.checked)} defaultChecked />}
              label="LowerCase"
            />
            <FormControlLabel
              control={<Checkbox checked={chattyValue} onChange={(e) => setChattyValue(e.target.checked)} />}
              label="Chatty"
            />
          </Grid>
          <Grid item xs={12}>
            <TextField
              id="outlined-start-adornment"
              sx={{ m: 1, width: '20ch' }}
              InputProps={{
                startAdornment: <InputAdornment position="start">Start</InputAdornment>
              }}
              variant="standard"
              defaultValue={'\\b'}
              onInput={(event) => setStartValue(event.target.value)}
            />
            <TextField
              id="outlined-start-adornment"
              sx={{ m: 1, width: '20ch' }}
              InputProps={{
                startAdornment: <InputAdornment position="start">End</InputAdornment>
              }}
              variant="standard"
              defaultValue={'\\b'}
              onInput={(event) => setEndValue(event.target.value)}
            />
          </Grid>
          <Grid item xs={12}>
            {outputMessage && (
              <Box
                sx={{
                  backgroundColor: 'rgb(250,250,250,0.1)',
                  padding: '2.5px',
                  paddingX: '15px',
                  whiteSpace: 'pre-wrap',
                  maxWidth: '100%',
                  overflowX: 'auto',
                  borderRadius: '5px'
                }}
              >
                <pre style={{ userSelect: 'all', margin: 0 }}>
                  {outputMessage.split('\n').map((line, index) => (
                    <React.Fragment key={index}>
                      <span style={{ marginRight: '1px', userSelect: 'none' }}>{index + 1}.</span>
                      <span>{line}</span>
                      <br />
                    </React.Fragment>
                  ))}
                </pre>
                {outputMessage && (
                  <>
                    <Button variant="text" fullWidth onClick={handleCopyClick}>
                      Copy
                    </Button>
                    {isCopied && (
                      <Snackbar open={isCopied} autoHideDuration={6000} onClose={handleClose}>
                        <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
                          Copied to clipboard!
                        </Alert>
                      </Snackbar>
                    )}
                  </>
                )}
              </Box>
            )}
          </Grid>
        </Grid>
      </Typography>
    </MainCard>
  );
};

export default Page;
